.reasons {
  display: flex;
  flex-direction: column;
  justify-content: center;

  &__description {
    margin-top: 15px;
    margin-bottom: 0;
    line-height: 21px;
  }

  &__picture {
    display: block;
    width: 100%;
    height: 260px;
    background-color: #daeaf2;
  }

  &__subtitle {
    margin: 0;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 15px;
    font-size: @font-size-title;
    font-weight: bold;
  }

  &__wrapper {
    padding-top: 45px;
    padding-bottom: 40px;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
    color: #ffffff;
    background-color: @block-bg-color;

    &--mask {
      background-size: 320px 37px;
      background-position: 50% -22px;
      background-repeat: no-repeat;
      background-image: url("../img/bg-triangle.svg");
    }
  }
}

.reasons:first-of-type .reasons__wrapper {
  padding-top: 60px;
}

@media (min-width: @tablet-width) {
  .reasons {
    flex-direction: row;
    justify-content: flex-start;
    background-color: #daeaf2;

    &__title {
      font-size: 21px;
      line-height: 26px;
      margin-bottom: 10px;
    }

    &__picture {
      height: 256px;
    }

    &__description {
      margin-top: 25px;
    }

    &__wrapper {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      padding-left: 40px;
      padding-right: 40px;
      width: 256px;
      height: 256px;
      box-sizing: border-box;

      &--mask {
        background-image: none;
      }
    }
  }

  .reasons:nth-child(4) {
    .reasons {
      &__wrapper {
        order: 1;
      }

      &__picture {
        order: 2;
      }
    }
  }

  .reasons:first-of-type .reasons__wrapper {
    padding-top: 45px;
  }
}

@media (min-width: @desktop-width) {
  .reasons {
    &__title {
      width: 200px;
      margin: 0 auto;
    }

    &__subtitle {
      margin-top: 15px;
      margin-bottom: 20px;
    }

    &__description {
      width: 280px;
      margin: 0 auto;
    }

    &__wrapper {
      padding-top: 50px;
      width: 400px;
    }
  }

  .reasons:first-of-type .reasons__wrapper {
    padding-top: 50px;
  }
}
