.reasons-list {
  background-color: #eeeeee;

  &__description {
    margin: auto;
    line-height: 21px;
    width: 250px;
  }

  &__item {
    padding-bottom: 30px;
    border-bottom: 1px solid #d6d6d6;
  }

  &__item:last-of-type {
    border: none;
  }

  &__list {
    text-align: center;
    margin: 0;
    padding: 30px 20px 0 20px;
    list-style: none;
  }

  &__subtitle {
    margin: 15px 0 15px 0;
  }

  &__title {
    margin: 30px 0 0 0;
    font-size: @font-size-title;
    font-weight: bold;
  }

  &__item:first-of-type &__title {
    margin-top: 0;
  }
}

@media (min-width: @tablet-width) {
  .reasons-list {
    &__description {
      width: 159px;
    }

    &__item {
      padding: 50px 0 40px 0;
      border: none;
    }

    &__list {
      display: flex;
      align-items: flex-start;
      justify-content: space-around;
      padding: 0;
    }

    &__subtitle {
      margin-top: 15px;
      margin-bottom: 25px;
    }

    &__title {
      width: 160px;
      margin: auto;
      font-size: 21px;
    }

    &__item:first-of-type &__title {
      margin-top: 0;
    }
  }
}

@media (min-width: @desktop-width) {
  .reasons-list {
    &__description {
      width: 280px;
    }

    &__item {
      padding: 45px 0 65px 0;
      border: none;
    }
  }
}
