.advices {
  background-color: #ffffff;

  &__description {
    line-height: 21px;
    margin: 15px 0 30px 0;
  }

  &__item br {
    display: none;
  }

  &__item {
    position: relative;

    &--food {
      position: absolute;
      top: -8px;
      left: -52px;
      width: 40px;
      height: 35px;
      fill: @block-bg-color;
    }

    &--house {
      position: absolute;
      top: -10px;
      left: -48px;
      width: 37px;
      height: 36px;
      fill: @block-bg-color;
    }

    &--souvenirs {
      position: absolute;
      top: -13px;
      left: -45px;
      width: 32px;
      height: 38px;
      fill: @block-bg-color;
    }
  }

  &__item:last-of-type .advices__description {
    margin-bottom: 0;
  }

  &__list {
    box-sizing: border-box;
    margin: auto;
    padding: 40px 40px 40px 70px;
    list-style: none;
    width: 320px;
  }

  &__title {
    margin: 0;
    font-size: @font-size-title;
    font-weight: bold;
  }
}

@media (min-width: @tablet-width) {
  .advices {
    &__description {
      margin-top: 20px;
      margin-bottom: 0;
    }

    &__item br {
      display: none;
    }

    &__item {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      text-align: center;
      width: 165px;
      padding: 100px 0 0 0;

      &--food {
        top: 0;
        left: 40px;
        width: 77px;
        height: 68px;
      }

      &--house {
        top: 0;
        left: 40px;
        width: 75px;
        height: 72px;
      }

      &--souvenirs {
        top: -3px;
        left: 47px;
        width: 64px;
        height: 75px;
      }
    }

    &__item:nth-child(1) {
      order: 1;
    }

    &__item:nth-child(2) {
      order: 3;
    }

    &__item:nth-child(3) {
      order: 2;
    }

    &__list {
      display: flex;
      justify-content: space-around;
      width: 100%;
      padding: 60px 0 40px 0;
    }

    &__title {
      font-size: 21px;
    }
  }
}

@media (min-width: @desktop-width) {
  .advices {
    padding-bottom: 45px;

    &__item {
      width: 290px;

      &--food {
        top: 3px;
        left: 105px;
      }

      &--house {
        top: 0;
        left: 105px;
      }

      &--souvenirs {
        top: -3px;
        left: 112px;
      }
    }

    &__item br {
      display: block;
    }
  }
}
